[data-branchpoints] {
  background-color: var(--almost-white);
  min-height: 14rem;
  padding: 2rem 0 2rem;
  font-family: var(--subtitle-font);
}

[data-branchpoints] div {
  margin: 0 auto;
}

[data-branchpoints] div .point-plus {
  display: inline-block;
  margin: 1rem;
  border-radius: 999px;
  background-color: var(--almost-white);
  width: 4rem;
  padding: .6rem .4rem;
  text-align: center;
  cursor: pointer;
  -webkit-transition: .1s;
  transition: .1s;
}

[data-branchpoints] div .point-plus:hover {
  background-color: var(--light-gray);
}

[data-branchpoints] .point {
  border: 1px solid var(--light-gray);
  border-radius: 6px;
  background-color: var(--almost-white);
  width: 10rem;
  padding: .2rem .4rem;
  margin: .8rem;
  position: relative;
}

[data-branchpoints] .point h3 {
  font-size: 1rem;
  margin: 0;
  padding-right: 1rem;
  border-right: 1px solid var(--light-gray);
  float: left;
  display: block;
  cursor: default;
}

[data-branchpoints] .point svg {
  position: absolute;
  top: 5px;
  right: 5px;
  color: var(--gray);
}

[data-branchpoints] .point svg:hover {
  color: var(--crab);
  cursor: pointer;
}

[data-branchpoints]:nth-child(even) {
  background-color: var(--almost-white);
}

[data-branchpoints] h1 {
  text-align: center;
}
