.ticket {
  width: 400px;
  margin: 0 auto;
  padding: 1rem;
  text-align: center;
  border: 1px dashed var(--light-gray);
  color: var(--gray);
}

.ticket .doc-body {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 4fr 1fr 1fr;
      grid-template-columns: 1fr 4fr 1fr 1fr;
  -ms-grid-rows: (1fr)[11];
      grid-template-rows: repeat(11, 1fr);
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 0 1px;
  overflow: hidden;
}

.ticket .doc-body data {
  padding: 3px 0;
  text-align: center;
}
